import { Streamlit } from "streamlit-component-lib"
import { useStreamlit } from "streamlit-component-lib-react-hooks"
import React, { useState, useCallback, useEffect, useMemo } from "react"

import { VTKViewer } from "lavender-vtkjs"

const VTKStreamlit: React.VFC = () => {

  const renderData = useStreamlit()

  const [type, setType] = React.useState<string>('vtkjs')

  const file = useMemo(() => {
    if (renderData && renderData.args["file"]) {
      return new Blob([renderData.args["file"]])
    }
    else {
      return new Blob()
    }
  }, [renderData])

  const menu = useMemo(() => {
    if (renderData && typeof renderData.args["menu"] !== 'undefined') {
      return renderData.args["menu"]
    }
    else {
      return true
    }
  }, [renderData])

  if (renderData == null) {
    return null
  }

  return (
    <div style={{ width: '100%', height: '100%', border: '1px solid #d0d7de', borderRadius: 2 }}>
      <VTKViewer file={file} type={type} menu={menu} />
    </div>
  )
}

export default VTKStreamlit
